@use "base/variables" as v;
@use "base/mixins" as m;

.video {
  position: relative;
  max-height: 50rem;
  min-height: 35rem;
  overflow: hidden;

  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgb(2, 0, 36);
    background: linear-gradient(
      270deg,
      rgba(2, 0, 36, 1) 0%,
      rgba(236, 23, 160, 0.3) 0%,
      rgba(189, 21, 194, 0.3) 100%
    );
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  video {
    display: block;
    width: auto;

    @include m.computador{
        width: 100%;

    }
    
  }
}

.contenido-video {
    z-index: 10 ;
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;

    }

    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2,4rem;
        margin: 0;

    }

}